  mounted() {
    // window.extAsyncInit = () => {
    this.interval = setInterval(() => {
      if (!this.fb_id) {
        // the Messenger Extensions JS SDK is done loading
        this.me = MessengerExtensions
        MessengerExtensions.getContext(
          // '902252186774664', // Let's Movie ÈõªÂΩ±Á¥ÑÊúÉÂÖßÈÉ®Ê∏¨Ë©¶ BOT ID
          '1405269929631051', // Let's Movie ÈõªÂΩ±Á¥ÑÊúÉ BOT ID
