                  'Content-Type': 'application/json',
                },
                method: 'POST',
                body: JSON.stringify({ fb_id: this.fb_id }),
              },
            )
              .then(res => {
                return res.json()
              })
              .then(res => {
                if (res.err) {
                  // const h = this.$createElement
                  this.$notify({
                    title: res.err,
                  })
                } else {
                  // console.log(res.data)
                  this.gender = res.data.gender
                }
              })
          },
          err => {
            console.log(err)
            this.fb_id = ''
